
::-webkit-input-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
::-moz-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
/* firefox 19+ */
:-ms-input-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
/* ie */
input:-moz-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
#app {
  font-family: 'Roboto', sans-serif;
  color: #484848;
  background-color: #FFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  outline: none;
}
a {
  font-weight: 400;
  color: #317fa9;
}
a:hover {
  cursor: pointer;
  color: #317fa9;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
img {
  width: 100%;
}
iframe {
  width: 100%;
  height: 180px;
}
input {
  border-radius: 3px;
  border: 1px solid #dbdbdb;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 8px 12px;
  outline: none;
}
input[type="file"] {
  display: none;
}
input:disabled {
  background-color: #efefef;
}
textarea {
  resize: vertical;
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
}
select {
  width: 100%;
  background-color: #FFF;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 8px 12px;
}
#ui-datepicker-div {
  background-color: #FFF !important;
  border: 1px solid #dbdbdb !important;
  width: 300px;
}
.button-orange {
  border: none;
  background-color: #f2583d;
  color: #FFF;
  border-radius: 3px;
  padding: 10px 28px;
  outline: none;
}
.button-orange:hover {
  background-color: #f2583d;
  color: #FFF;
}
.button-orange:focus {
  background-color: #f2583d;
  color: #FFF;
}
.button-orange:active {
  background-color: #f2583d;
  color: #FFF;
}
.button-white {
  border: 1px solid #f2583d;
  background-color: #FFF;
  color: #f2583d;
  border-radius: 3px;
  padding: 10px 28px;
  outline: none;
}
.button-white:hover {
  background-color: #FFF;
  color: #f2583d;
}
.button-white:focus {
  background-color: #FFF;
  color: #f2583d;
}
.button-white:active {
  background-color: #FFF;
  color: #f2583d;
}
.contents {
  position: relative;
  max-width: 1040px;
  margin: 60px auto;
  padding: 0 12px;
}
.detail-contents {
  position: relative;
  max-width: 1040px;
  margin: 15px auto;
  padding: 0 12px;
}
.narrow-contents {
  position: relative;
  max-width: 800px;
  margin: 60px auto;
  padding: 0 12px;
}
.btn,
.btn-lg {
  background-color: #f2583d;
  color: #FFF;
  border: none;
}
.btn:hover,
.btn-lg:hover {
  background-color: #f2583d;
  color: #FFF;
}
.btn:focus,
.btn-lg:focus {
  background-color: #f2583d;
  color: #FFF;
}
.divider {
  margin: 18px 0 30px 0;
  width: 100%;
  height: 1px;
  background-color: #dbdbdb;
  display: block;
}
/*Google maps API Hard Control*/
.gm-style-pbc {
  opacity: 0 !important;
}
@media (min-width: 744px) {
iframe {
    width: 100%;
    height: 440px;
}
.contents {
    padding: 0 24px;
}
.detail-contents {
    position: relative;
    padding: 0 24px;
}
.main-contents {
    position: relative;
    padding: 0 24px;
}
}
@media (min-width: 1128px) {
iframe {
    width: 100%;
    height: 544px;
}
.contents {
    padding: 0;
}
.detail-contents {
    position: relative;
    padding: 0;
}
.main-contents {
    position: relative;
    padding: 0;
}
}



